import { Link } from "gatsby"
import PropTypes from "prop-types"
import React from "react"
import headerStyles from './header.module.css'
import linkedin from '../images/linkedin-3-32.png'
import instagram from '../images/insta_icon_32px.png'
import github from '../images/GitHub-Mark-Light-32px.png'
import twitter from '../images/twitter32.png'

const Header = ({ siteTitle }) => (
  <header className={headerStyles.header} >

    <div className={headerStyles.title} >
      <h1 style={{ margin: 0 }}>
        <Link to="/" style={{
            color: `white`,
            textDecoration: `none`,
          }}>
          {siteTitle}
        </Link>
      </h1>
    </div>

    <div className={headerStyles.socialContainer}>
      <a href="https://twitter.com/drewisatlas" className={headerStyles.socialIconLink}> <img src={twitter} alt="twitter link"/> </a>
      <a href="https://instagram.com/drewisatlas" className={headerStyles.socialIconLink}> <img src={instagram} alt="instagram link"/> </a>
      <a href="https://github.com/Drewisatlas" className={headerStyles.socialIconLink}> <img src={github} alt="github link"/> </a>
      <a href="https://www.linkedin.com/in/andrewnrivera/" className={headerStyles.socialIconLink}> <img src={linkedin} alt="linkedin link"/> </a>
     </div>
  </header>
)

Header.propTypes = {
  siteTitle: PropTypes.string,
}

Header.defaultProps = {
  siteTitle: `Andrew Rivera`,
}

export default Header
